@import url(https://fonts.googleapis.com/css?family=Poppins:400,700);
:root {
  --bg-color: darkslateblue;
  --dm-color: lightcoral;
}

body {
  background: darkslateblue;
  background: var(--bg-color);
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.drum-pad {
  background: rgba(255, 255, 240, 0.5);
  color: rgba(0, 0, 0, 0.6);
  border-radius: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.4em;
  font-weight: 700;
  min-width: 150px;
  min-height: 150px;
  margin: 5px;
  text-transform: uppercase;
}

.drum-pad:hover {
  cursor: pointer;
}

.playing {
  -webkit-animation: highlight 100ms ease-out forwards;
          animation: highlight 100ms ease-out forwards;
}

@-webkit-keyframes highlight {
  100% {
    background: rgba(255, 255, 240, 0.7);
  }
}

@keyframes highlight {
  100% {
    background: rgba(255, 255, 240, 0.7);
  }
}

#drum-machine {
  background: var(--dm-color);
  border: 1px solid rgba(0, 0, 0, 0.6);
  border-radius: 5px;
  box-shadow: 0px 5px 12px rgba(0, 0, 0, 0.3);
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#drum-machine-controls {
  background: rgba(0, 0, 0, 0.03);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 10px;
}

#display {
  background: rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(0, 0, 0, 0.2);
  color: rgba(0, 0, 0, 0.6);
  font-weight: 700;
  padding: 10px 15px;
  text-transform: uppercase;
  text-align: center;
  width: 100px;
}

#volume {
  -webkit-appearance: none;
  background: rgba(255, 255, 240, 0.5);
  outline: none;
  width: 200px;
  height: 25px;
}

#volume::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  background: var(--bg-color);
  cursor: pointer;
  width: 25px;
  height: 25px;
}

#volume::-moz-range-thumb {
  background: var(--bg-color);
  border: 1px solid var(--bg-color);
  border-radius: 0%;
  cursor: pointer;
  width: 25px;
  height: 25px;
}
#volume::-moz-range-track { background: none; }

.drum-pad-container {
  padding: 10px 20px;
}

.drum-pad-row {
  display: flex;
  justify-content: center;
}

#App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

header, footer {
  background-color: rgba(0, 0, 0, 0.2);
  color: rgba(255, 255, 255, 0.9);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

header {
  font-size: calc(1.5em + 1vmin);
  text-transform: uppercase;
}
header h1 { margin: 0; }

footer {
  font-size: 0.9em;
  height: 40px;
  text-transform: lowercase;
}

footer a {
  color: inherit;
  text-decoration: none;
  -webkit-transition: color 300ms ease-out;
  transition: color 300ms ease-out;
}
footer a:hover { color: lightgreen; }

#content {
  display: flex;
  flex: 1 1;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

